.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  font-family: 'Lato','Roboto',Helvetica;
  }

.flex{display: -ms-flexbox;display: flex;} .italic{font-style: italic}
.row{-ms-flex-direction: row;flex-direction: row;}

/*Setup*/
.highLight{
  text-decoration: underline;-webkit-text-decoration-color:#0e5a60;text-decoration-color:#0e5a60;
}
.menuWrapper{display: -ms-flexbox;display: flex;-ms-flex-direction: column;flex-direction: column;}
.mainMenu{
  width: 200px;
  height: 100vh;
  background-color: #333;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-box-shadow: 0 2px 1px 1px 	#0e5a60;
          box-shadow: 0 2px 1px 1px 	#0e5a60;
}
.main{overflow: auto;}
.main p{text-align: center;max-width: 80%; margin: 18px auto}
.ramonWrapper{font-family:'Roboto';}
.ramonWrapper span{color: #0e5a60}
.menuBtn{
  background-color: inherit;
  color: #ddd;
  border: none;
  margin-bottom: 1px;
  cursor: pointer;
  padding: 5px;
  font-family: 'Lato';
  border-bottom: 1px solid 	#0e5a60;
}
.menuBtn:hover{
  background-color: rgba(255,255,255,0.3);}
/*.menuBtn:focus{color:#0e5a60;font-weight: bold;}*/
.contentWrapper{
  width:90%;margin: auto;max-width: 90%;
  overflow-y: auto;
  max-height: 100vh;
}
/**/
.portfolios{overflow: scroll;}
.casePort{display: -ms-flexbox;display: flex;-ms-flex-direction: row;flex-direction: row;-ms-flex-pack: center;justify-content: center;margin-bottom: 2vh;}
.casePort:nth-last-child(1){margin-bottom: 0;}
.casePort:nth-child(even){-ms-flex-direction: row-reverse;flex-direction: row-reverse;background-color: rgba(14,90,96,0.7)}
.casePort div{margin: auto;}
/**/

/**/
.main{
overflow: hidden;
width: 100%;
}
.mainTutoriais{max-height: 200px;display: -ms-flexbox;display: flex;-ms-flex-pack: distribute;justify-content: space-around;}
.mainTutoriais img{max-height: 100%;}
.slideWrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: auto;
}
.slide{
  position: relative;
  left: -100%;
  min-width: 100%;
  -webkit-transition: 0.45s;
  -o-transition: 0.45s;
  transition: 0.45s;
}
.slide h2,.slide h4{color: #222;}
.slide h2{font-size: 1.6em;}
.slide h2{font-style: italic;}
.boxFade{padding: 5px;border-radius: 5px;color: #eee;background-color:#0e5a60;margin: 0}
/**/


@media screen and (max-width: 550px){
  .App{
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .mainMenu{
    min-width: 100vw;
    max-height: 15vh;
    height: auto;
    padding: 10px 0;
    background-color: #333;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .menuBtn{
    border: none;
    border-bottom: none;
    margin-right: 5px;
  }
  .contentWrapper{
    width:100%;max-width: 100%;
    overflow-y: auto;
    max-height: 100vh;
  }
  .menuBtn:nth-last-child(1){margin-right: 0px;}
  .ramonWrapper h1, .ramonWrapper h3{margin: 0;}
  .ramonWrapper h1{font-size: 1.3em;}
  .ramonWrapper h3{font-size: 1em;}
  .menuWrapper{-ms-flex-direction: row;flex-direction: row;}
}



